import DrawerBase from "../../drawer";
import { useModalPro } from "../../../lib";
import { Fragment, ReactNode, Ref, useEffect, useRef } from "react";
import { ProSheetModalProps } from "../../types";

type ProSheetExtendedProps = {
    children: ReactNode;
    TriggerElement: ReactNode;
}

const ProSheet = ({ TriggerElement, direction, children, ...props }: ProSheetModalProps & ProSheetExtendedProps) => {

    const drawerRef = useRef<HTMLDivElement>(undefined)

    const { handleOpenModal, handleCloseModal, currentModalKey, ...modalProps } = useModalPro({
        ...props,
        sheetRef: drawerRef,
        sidebarDirection: direction
    })

    useEffect(() => {
        if (modalProps.open) {
            document.body.style.overscrollBehavior = "none";
            return () => {
                document.body.style.overscrollBehavior = "initial";
            };
        }
    }, [modalProps.open]);

    return (
        <Fragment>
            <div onClick={handleOpenModal}>
                {TriggerElement}
            </div>
            <DrawerBase
                {...modalProps}
                direction={direction}
                key={currentModalKey}
                handleClose={handleCloseModal}
                ref={drawerRef as Ref<HTMLDivElement | null>}>
                {children}
            </DrawerBase >
        </Fragment>
    )
}

export default ProSheet